<template>
  <div class="home">
    <div class="homeTop">
      <el-radio-group v-model="radio" size="medium" @change="changeTab">
        <el-radio-button label="大学城校区"></el-radio-button>
        <el-radio-button label="石牌校区"></el-radio-button>
        <el-radio-button label="南海校区"></el-radio-button>
      </el-radio-group>
      <!-- <el-input
        placeholder="请输入任务关键词"
        prefix-icon="el-icon-search"
        v-model="searchThis"
        class="clearfix searchBox"
      ></el-input> -->
    </div>
    <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">
      <li v-for="(item,index) in list" class="infinite-list-item" :key="index">
        <el-card class="box-card box">
          <div slot="header" class="clearfix">
            <span>{{item.title}}</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="accept(item)">接单</el-button>
          </div>
          <div class="text item">{{item.detail}}</div>
        </el-card>
      </li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from "@/components/HelloWorld.vue";
import mytask from "@/views/paotui/myTask.vue";
import publish from "@/views/paotui/publish.vue";
import axios from "axios";
export default {
  name: "Home",
  data() {
    return {
      count: 0,
      radio: "大学城校区",
      searchThis: "",
      list: []
    };
  },
  created() {
    //初次渲染列表数据
    this.list = this.$store.getters.getTaskList(this.radio);
    this.getTasks();
  },
  methods: {
    load() {},
    getTasks() {
      axios({
        url: "http://39.108.108.227:9527/cd/directory/getFiles/0",
        methods: "get"
      })
        .then(res => {
          console.log(res);
          console.log("ok?");
        })
        .catch(err => {
          console.log("wrong");
        });
    },
    accept(newTask) {
      if (this.$store.state.user.token) {
        this.$confirm("是否确认接单 ?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "接单成功，要按时完成单子哦!(*╹▽╹*)"
            });
            console.log(newTask);
            this.$store.dispatch("addAcceptFun", newTask);
            this.list = this.$store.getters.getTaskList(this.radio);
          })
          .catch(() => {
            console.log("somethingwrong");
            this.$message({
              type: "info",
              message: "已取消接单啦(*╹▽╹*)"
            });
          });
      } else {
        this.$message({
          type: "info",
          message: "请登录"
        });
      }
    },
    changeTab() {
      //切换标签修改列表入口
      this.list = this.$store.getters.getTaskList(this.radio);
    }
  }
};
</script>

