<template>
  <div>
    <el-radio-group v-model="myListRadio" size="medium" @change="changeList">
      <el-radio-button label="我的接单" >我的接单</el-radio-button>
      <el-radio-button label="我发布的任务" >我发布的任务</el-radio-button>
    </el-radio-group>
    <ul class="infinite-list" style="overflow:auto">
      <li v-for="(item,index) in list" class="infinite-list-item" :key="index">
        <el-card class="box-card box">
          <div slot="header" class="clearfix">
            <span>{{item.title}}</span>
          </div>
          <div class="text item">{{item.detail}}</div>
        </el-card>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      status: true,
      myListRadio:'我的接单',
      list:[]
    }
  },
  created(){
    console.log('jiazai?')
    this.changeList()
  },
  methods: {
    changeList(){
      if(this.myListRadio=="我的接单"){
        //数据渲染地方
        this.list = this.$store.getters.getUserAccept
      }else if(this.myListRadio=="我发布的任务"){
        //数据渲染地方
        this.list = this.$store.getters.getUserPublish
      }
    }
  }
};
</script>

