<template>
  <form class="publish">
    <span>请输入任务标题:</span>
    <el-input v-model="newTask.title" placeholder="例：“打印一份文件送到南五4楼”"></el-input>
    <span>请选择校区：</span>
    <el-radio-group v-model="radio">
      <el-radio :label="3">大学城校区</el-radio>
      <el-radio :label="6">石牌校区</el-radio>
      <el-radio :label="9">南海校区</el-radio>
    </el-radio-group>
    <el-input
      type="textarea"
      :rows="17"
      placeholder="请输入任务详情"
      v-model="newTask.detail"
      resize="none"
      class="detail"
    ></el-input>
    <el-button type="primary" round class="publishbtn" @click="publish">发布</el-button>
  </form>
</template>

<script>
export default {
  name: "publish",
  data() {
    return {
      radio: 3,
      newTask: {
        title: "",
        area: "",
        detail: "",
        publisher: "Info",
        finishTime: "2020-06-21 22:00"
      },
      flag: true
    };
  },
  methods: {
    publish() {
      var that = this;
      const loadingObj = this.$loading({
        lock: true,
        text: "发布中，请稍后......",
        spinner: "el-icon-loading",
        background: "rgba(0,0,0,0.7)"
      });
      setTimeout(function() {
        loadingObj.close();
        if (that.flag == true) {
          that.$message({
            message: "发布成功",
            type: "success"
          });
        } else {
        }
      }, 2000);
      setTimeout(function(){
        that.$router.push({path:'/'})
      },2100);
      if (this.radio == "3") {
        this.newTask.area = "大学城校区";
      } else if (this.radio == "6") {
        this.newTask.area = "石牌校区";
      } else if (this.radio == "9") {
        this.newTask.area = "南海校区";
      }
      //发布入口，newTask
      this.$store.dispatch("addPublishTaskFun", this.newTask);
    }
  }
};
</script>

<style scoped>
.el-radio .el-radio__inner {
  border: 2px solid #dcdfe6;
  width: 16px;
  height: 16px;
}
.publish span {
  display: inline-block;
  margin-top: 10px;
  margin-right: 10px;
}
.el-input {
  margin-top: 10px;
}
.detail {
  margin-top: 10px;
  border: 1px solid #dcdfe6; /* no */
}
.publishbtn {
  margin-top: 5px;
  width: 100px;
  float: right;
}
</style>