import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
//创建vuex实例
const store = new Vuex.Store({
    state: {
        user: {
            tel: window.sessionStorage.getItem('tel'),
            password: window.sessionStorage.getItem('password'),
            token: window.sessionStorage.getItem('token'),
            myPublish: [{
                title: '我发布的帮忙买一份小土豆',
                finishTime: '2020-06-11 22:00',
                area: '大学城校区',
                detail: '我发布的微辣，送到南五',
                publisher: 'vera'
            }],
            myAccept: [{
                title: '我接受的帮忙买一份小土豆',
                finishTime: '2020-06-11 22:00',
                area: '大学城校区',
                detail: '我接受的微辣，送到南五',
                publisher: 'vera'
            }]
        },
        taskList: [{
            title: '帮忙买一份小土豆大学城',
            finishTime: '2020-06-11 22:00',
            area: '大学城校区',
            detail: '微辣，送到南五',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件大学城',
            finishTime: '2020-06-15 16:00',
            area: '大学城校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件大学城',
            finishTime: '2020-06-15 16:00',
            area: '大学城校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件大学城',
            finishTime: '2020-06-15 16:00',
            area: '大学城校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件大学城',
            finishTime: '2020-06-15 16:00',
            area: '大学城校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '买一份炸酱面石牌',
            finishTime: '2020-06-15 16:00',
            area: '石牌校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件南海',
            finishTime: '2020-06-15 16:00',
            area: '南海校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }, {
            title: '帮忙打印一份文件石牌',
            finishTime: '2020-06-15 16:00',
            area: '石牌校区',
            detail: '送到教五栋101',
            publisher: 'vera'
        }]
    },
    getters: {
        getTaskList: (state) => (area) => {
            return state.taskList.filter(task => task.area == area)
        },
        getUserInfo: state => {
            let user = { tel: state.user.tel, token: state.user.token };
            return user
        },
        getUserPublish: state => {
            console.log(state.user.myPublish)
            return state.user.myPublish
        },
        getUserAccept: state => {
            console.log(state.user.myAccept)
            return state.user.myAccept
        }
    },
    mutations: {
        login(state, loginUser) {
            window.sessionStorage.setItem('tel', loginUser.tel)
            window.sessionStorage.setItem('password', loginUser.password)
            window.sessionStorage.setItem('token', loginUser.token)
            store.state.user.password=loginUser.password
            store.state.user.tel=loginUser.tel
            store.state.user.token=loginUser.token

        },
        logout: (state) => {
            store.state.user.password=''
            store.state.user.tel=''
            store.state.user.token=false
            window.sessionStorage.removeItem('token')
            window.sessionStorage.removeItem('password')
            window.sessionStorage.removeItem('tel')
        },
        addPublishTask: (state, newTask) => {
            state.taskList.unshift(newTask)
            state.user.myPublish.unshift(newTask)
        },
        addAccept: (state, newTask) => {
            state.user.myAccept.unshift(newTask)
            state.taskList.splice(state.taskList.indexOf(newTask), 1)
            console.log(state.taskList)
        },
    },
    actions: {
        loginFun(state, loginUser) {
            if (loginUser.tel == '15626131152' && loginUser.password == '123456') {
                loginUser.token = true
                state.commit('login', loginUser)
            }
        },
        addPublishTaskFun(state, newTask) {
            state.commit('addPublishTask', newTask)
        },
        addAcceptFun(state, newTask) {
            state.commit('addAccept', newTask)
        },
        logout({commit}){ 
            commit('logout')
        }
    }
})
//导出store
export default store